@charset "UTF-8";
/* stylelint-disable */
:root {
  --font-family: "Onest", sans-serif;
  --font-second: "Benzin", sans-serif;
  --font-third: "BasisGrotesqueArabicPro", sans-serif;
  --font-fourth: "PPEditorialNew", sans-serif;
  --content-width: 1232px;
  --container-offset: 1rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --fs-base: .875rem;
  --fw-base: 400;
  --lh-base: normal;
  --title-color: #000;
  --text-color: #282828;
  --text-color-05: rgba(40, 40, 40, 0.05);
  --text-color-10: rgba(40, 40, 40, 0.1);
  --text-color-20: rgba(40, 40, 40, 0.2);
  --text-color-50: rgba(40, 40, 40, 0.5);
  --text-color-70: rgba(40, 40, 40, 0.7);
  --primary: #D51E28;
  --primary-20: rgba(213, 30, 40, 0.2);
  --primary-50: rgba(213, 30, 40, 0.5);
  --dark: #000;
  --dark-20: rgba(0, 0, 0, 0.2);
  --dark-40: rgba(0, 0, 0, 0.4);
  --light: #fff;
  --light-05: rgba(255, 255, 255, 0.05);
  --light-10: rgba(255, 255, 255, 0.1);
  --light-20: rgba(255, 255, 255, 0.2);
  --gray: #808080;
  --gray-200: #f5f5f5;
  --error: #EF4E40;
  --border-color: var(--text-color-10);
  --backdrop: var(--light-10);
  --body-bg: var(--gray-200);
  --rounded: 1rem;
  --shadow: 0rem 0rem .625rem .125rem rgba(0,0,0,0.375);
  --shadow-sm: 0rem 0rem .625rem .125rem rgba(0,0,0,0.25);
  --transition-duration: 300ms;
  --transition-timing-function: ease-in-out;
}

@media (min-width: 991.98px) {
  :root {
    --fs-base: 1rem;
  }
}
@font-face {
  font-family: "Onest";
  src: url("../fonts/Onest-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Onest";
  src: url("../fonts/Onest-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Onest";
  src: url("../fonts/Onest-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Benzin";
  src: url("../fonts/Benzin-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Benzin";
  src: url("../fonts/Benzin-Semibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "BasisGrotesqueArabicPro";
  src: url("../fonts/BasisGrotesqueArabicPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "BasisGrotesqueArabicPro";
  src: url("../fonts/BasisGrotesqueArabicPro-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "PPEditorialNew";
  src: url("../fonts/PPEditorialNew-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page-body {
  min-width: 20rem;
  min-height: 100%;
  margin: 0;
  font-family: var(--font-family, sans-serif);
  font-size: var(--fs-base);
  font-weight: var(--fs-base);
  line-height: var(--lh-base);
  color: var(--text-color);
  letter-spacing: -0.02em;
  background-color: var(--body-bg);
}

.page-body,
.site-container,
.main {
  display: flex;
  flex-direction: column;
}

.site-container,
.main,
.main-section {
  flex: 1;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.hover-underline {
  --_underline-size: .0625em;
  --_underline-offset: .125em;
}
.hover-underline .text {
  background-image: linear-gradient(90deg, currentColor, currentColor);
  background-size: 0% var(--_underline-size);
  background-repeat: no-repeat;
  background-position: left 100%;
  margin-bottom: calc(var(--_underline-offset) * -1);
  padding-bottom: var(--_underline-offset);
  transition-property: background-size;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
}
@media (hover: hover) {
  .hover-underline:is(:hover, :focus-visible) .text {
    background-size: 100% var(--_underline-size);
  }
}

.underline {
  --_underline-size: .0625em;
  --_underline-offset: 0rem;
}
.underline .text {
  background-image: linear-gradient(90deg, currentColor, currentColor);
  background-size: 100% var(--_underline-size);
  background-repeat: no-repeat;
  background-position: left 100%;
  margin-bottom: calc(var(--_underline-offset) * -1);
  padding-bottom: var(--_underline-offset);
}

.is-hidden {
  display: none !important;
}

.is-invisible {
  opacity: 0 !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(0 0 0 0) !important;
}

.text-underline {
  text-decoration-line: underline !important;
  text-decoration-thickness: 0.0625em;
  text-underline-offset: 0.125em;
}

.stretched-link {
  position: absolute;
  inset: 0;
  z-index: 5;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.centered {
  text-align: center;
}

.text-primary {
  color: var(--primary) !important;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

h1, h2, h3, h4, h5 {
  font-family: var(--font-second);
  font-weight: 600;
  line-height: 1.2;
  text-transform: uppercase;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

h1, .h1 {
  font-size: 1.125rem;
}
@media (min-width: 991.98px) {
  h1, .h1 {
    font-size: 3.75rem;
  }
}

h2, .h2 {
  font-size: 1.125rem;
}
@media (min-width: 991.98px) {
  h2, .h2 {
    font-size: 3.125rem;
  }
}

h4, .h4 {
  font-size: 1.125rem;
}
@media (min-width: 991.98px) {
  h4, .h4 {
    font-size: 2rem;
  }
}

h6, .h6 {
  font-size: 1.125rem;
}

p + p {
  margin-top: 1rem;
}

.section {
  margin-bottom: 2.5rem;
}
@media (min-width: 991.98px) {
  .section {
    margin-bottom: 7.5rem;
  }
}
.section-border::before {
  content: "";
  display: block;
  margin-inline: max(var(--container-offset), (100% - var(--content-width)) / 2);
  margin-bottom: 2.5rem;
  border-top: 1px solid var(--text-color-20);
}
.section-border-transparent::before {
  content: "";
  display: block;
  margin-inline: max(var(--container-offset), (100% - var(--content-width)) / 2);
  margin-bottom: 2.5rem;
  border-top: 1px solid transparent;
}
.section-header {
  display: flex;
  margin-bottom: 1.5rem;
}
@media (min-width: 991.98px) {
  .section-header {
    margin-bottom: 2.5rem;
  }
}
.section-header-more {
  display: flex;
  align-items: flex-end;
  gap: 1rem;
}
.section-header-more .btn-more {
  --_fs: .75rem;
}
@media (min-width: 991.98px) {
  .section-header-more .btn-more {
    --_fs: var(--fs-base);
  }
}
.section-title {
  flex: 1;
}
.section-title + .section-subtitle {
  margin-top: 1rem;
}
.section-subtitle {
  font-size: 1.5rem;
}
.section-more {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
}
.section-more:empty {
  display: none;
}

.icon {
  --_size: 1rem;
  --_color: currentColor;
  width: var(--_size);
  height: var(--_size);
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--_color);
}
.icon svg {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
  object-position: center;
}

.icon-container {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.icon-container .icon {
  transition-property: opacity;
  transition-duration: var(--_transition-duration, var(--transition-duration));
  transition-timing-function: var(--_transition-timing-function, var(--transition-timing-function));
}
.icon-container .icon:last-child {
  opacity: 0;
  position: absolute;
  inset: 0;
}

.btn {
  --_width: initial;
  --_height: initial;
  --_fs: var(--fs-base);
  --_fw: 300;
  --_lh: normal;
  --_ls: inherit;
  --_border-width: 0;
  --_border-style: solid;
  --_rounded: 0;
  --_padding: 0;
  --_transition-duration: var(--transition-duration);
  --_transition-timing-function: var(--transition-timing-function);
  --_color: var(--text-color);
  --_color-hover: var(--primary);
  --_color-active: var(--primary);
  --_bg: transparent;
  --_bg-hover: transparent;
  --_bg-active: transparent;
  --_border-color: transparent;
  --_border-color-hover: transparent;
  --_border-color-active: transparent;
  --_transform: translateZ(0);
  --_transform-hover: translateZ(0);
  --_transform-active: initial;
  --_icon-size: 1rem;
  --_icon-color: var(--_color);
  --_icon-hover-color: var(--_color-hover);
  --_icon-active-color: var(--_color-active);
  width: var(--_width);
  height: var(--_height);
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-size: var(--_fs);
  font-weight: var(--_fw);
  line-height: var(--_lh);
  letter-spacing: var(--_ls);
  color: var(--_color);
  cursor: pointer;
  background-color: var(--_bg);
  border: var(--_border-width) var(--_border-style) var(--_border-color);
  border-radius: var(--_rounded);
  padding: var(--_padding);
  pointer-events: auto;
  outline: none;
  text-align: start;
  transform: var(--_transform);
  transition-property: color, text-shadow, background, border, transform, opacity;
  transition-duration: var(--_transition-duration);
  transition-timing-function: var(--_transition-timing-function);
}
.btn .icon {
  --_size: var(--_icon-size);
  --_color: var(--_icon-color);
  transition-property: color;
  transition-duration: var(--_transition-duration);
  transition-timing-function: var(--_transition-timing-function);
}
@media (hover: hover) {
  .btn:is(:hover, :focus-visible) {
    color: var(--_color-hover);
    background-color: var(--_bg-hover);
    border-color: var(--_border-color-hover);
    transform: var(--_transform-hover);
  }
  .btn:is(:hover, :focus-visible) .icon {
    --_color: var(--_icon-hover-color);
  }
}
.btn:is(:active, .is-active) {
  color: var(--_color-active);
  background-color: var(--_bg-active);
  border-color: var(--_border-color-active);
  transform: var(--_transform-active);
}
.btn:is(:active, .is-active) .icon {
  --_color: var(--_icon-active-color);
}
.btn:disabled {
  pointer-events: none;
  opacity: 0.5;
}

.label-check {
  cursor: pointer;
}
.label-check .btn {
  pointer-events: none;
}

.btn-check {
  display: inline-flex;
}
.btn-check [type=checkbox]:checked + .btn,
.btn-check [type=radio]:checked + .btn {
  --_color: var(--primary);
  --_color-hover: var(--primary);
  --_color-active: var(--primary);
}
.btn-check [type=checkbox]:checked + .btn .icon-container .icon:first-child,
.btn-check [type=radio]:checked + .btn .icon-container .icon:first-child {
  opacity: 0;
}
.btn-check [type=checkbox]:checked + .btn .icon-container .icon:last-child,
.btn-check [type=radio]:checked + .btn .icon-container .icon:last-child {
  opacity: 1;
}
.btn-check [type=checkbox]:checked + .btn-outline-secondary,
.btn-check [type=radio]:checked + .btn-outline-secondary {
  --_color: var(--body-bg);
  --_bg: var(--text-color);
}
.btn-check .btn {
  flex: 1 1;
}

.btn-check-outline-favorite [type=checkbox]:checked + .btn,
.btn-check-outline-favorite [type=radio]:checked + .btn {
  --_color: var(--text-color);
  --_color-hover: var(--body-bg);
  --_color-active: var(--text-color);
  --_bg: transparent;
  --_icon-color: var(--primary);
  --_icon-hover-color: var(--primary);
  --_icon-active-color: var(--primary);
}

.btn-inline {
  --_color: inherit;
}

.btn-inherit {
  --_fs: inherit;
  --_fw: inherit;
  --_lh: inherit;
  --_ls: inherit;
  --_color: inherit;
  display: inline;
  text-transform: initial;
}

.btn-reset {
  --_fs: .75rem;
  --_color: var(--text-color-50);
}
@media (min-width: 991.98px) {
  .btn-reset {
    --_fs: .875rem;
  }
}

.btn-icon {
  --_width: 2rem;
  --_height: 2rem;
  --_rounded: 50%;
  --_padding: .625rem;
  --_icon-size: 100%;
  position: relative;
}
@media (min-width: 991.98px) {
  .btn-icon {
    --_width: 2.5rem;
    --_height: 2.5rem;
    --_padding: .75rem;
  }
}
.btn-icon .badge {
  position: absolute;
  top: 0;
  right: -0.25rem;
}
.btn-icon.btn-sm {
  --_width: 2rem;
  --_height: 2rem;
  --_padding: .625rem;
}
@media (min-width: 991.98px) {
  .btn-icon.btn-lg {
    --_width: 3rem;
    --_height: 3rem;
    --_padding: .75rem;
  }
}
.btn-icon.btn-menu {
  --_padding: .5rem;
}
@media (min-width: 991.98px) {
  .btn-icon.btn-menu {
    --_padding: .625rem ;
  }
}

.btn-location {
  --_icon-size: 1.25rem;
}
.btn-location .icon:not(.btn-location-dropdown) {
  --_color: var(--primary);
}
.btn-location-dropdown.icon {
  --_size: .75rem;
}

.btn-primary {
  --_color: var(--light);
  --_color-hover: var(--light);
  --_color-active: var(--light);
  --_bg: var(--primary);
  --_bg-hover: var(--text-color);
  --_bg-active: var(--text-color);
}

.btn-secondary {
  --_color: var(--light);
  --_color-hover: var(--light);
  --_color-active: var(--light);
  --_bg: var(--text-color);
  --_bg-hover: var(--primary);
  --_bg-active: var(--primary);
}
.btn-secondary:disabled {
  opacity: 0.2;
}

.btn-outline-secondary {
  --_border-width: .0625rem;
  --_border-color: var(--text-color-20);
  --_color: var(--text-color);
  --_color-hover: var(--light);
  --_color-active: var(--light);
  --_bg: transparent;
  --_bg-hover: var(--text-color);
  --_bg-active: var(--text-color);
  --_border-color-hover: var(--text-color);
  --_border-color-active: var(--text-color);
}

.btn-light {
  --_color-hover: var(--text-color);
  --_color-active: var(--text-color);
  --_bg: var(--light);
  --_bg-hover: #ccc;
  --_bg-active: #ccc;
}

.btn-gray-200 {
  --_color-hover: var(--light);
  --_color-active: var(--light);
  --_bg: var(--gray-200);
  --_bg-hover: var(--text-color);
  --_bg-active: var(--text-color);
}

.btn-blur-light {
  --_border-width: .0625rem;
  --_border-color: var(--light-20);
  --_color: var(--light);
  --_bg: var(--light-05);
  --_bg-hover: var(--light-05);
  --_bg-active: var(--light-05);
  --_border-color-hover: var(--light-20);
  --_border-color-active: var(--light-20);
  backdrop-filter: blur(0.25rem);
}

.btn-blur-secondary {
  --_border-width: .0625rem;
  --_color: var(--text-color);
  --_bg: var(--light-10);
  --_bg-hover: var(--light-10);
  --_bg-active: var(--light-10);
  --_border-color: var(--text-color-10);
  --_border-color-hover: var(--text-color-20);
  --_border-color-active: var(--text-color-20);
  backdrop-filter: blur(0.25rem);
}

.btn-volume .text {
  position: relative;
}
.btn-volume .text > span:last-child {
  opacity: 0;
  position: absolute;
}
@media (max-width: 576px) {
  .btn-volume .text {
    display: none;
  }
}
.btn-volume.is-active .icon-container .icon:first-child {
  opacity: 0;
}
.btn-volume.is-active .icon-container .icon:last-child {
  opacity: 1;
}
.btn-volume.is-active .text {
  position: relative;
}
.btn-volume.is-active .text > span:first-child {
  opacity: 0;
  position: absolute;
}
.btn-volume.is-active .text > span:last-child {
  opacity: 1;
  position: static;
}

.btn-sm:not(.btn-icon) {
  --_height: 2rem;
  --_fs: .75rem;
  --_rounded: .375rem;
  --_padding: .125rem .75rem;
  --_icon-size: 1rem;
  text-align: center;
  justify-content: center;
}

.btn-base:not(.btn-icon) {
  --_height: 2.25rem;
  --_fs: .875rem;
  --_rounded: .375rem;
  --_padding: .25rem 1rem;
  --_icon-size: 1.25rem;
  text-align: center;
  justify-content: center;
}
@media (min-width: 991.98px) {
  .btn-base:not(.btn-icon) {
    --_height: 2.5rem;
  }
}

.btn-lg:not(.btn-icon) {
  --_height: 2.25rem;
  --_rounded: .375rem;
  --_padding: .25rem 1.25rem;
  text-align: center;
  justify-content: center;
}
@media (min-width: 991.98px) {
  .btn-lg:not(.btn-icon) {
    --_height: 3rem;
    --_fs: var(--fs-base);
    --_padding: .25rem 1.5rem;
  }
}

.field-control,
.fieldset {
  position: relative;
}

.field-control {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.field-control:last-child .field-feedback {
  margin-bottom: 0;
}

.field-feedback {
  font-size: 0.75rem;
  line-height: 1.2;
  color: var(--text-color-50);
  margin-bottom: 0.5rem;
}
.field-feedback--error {
  color: var(--error);
}

.field {
  --_width: 100%;
  --_height: 3rem;
  --_fs: .875rem;
  --_fw: var(--fw-base);
  --_lh: var(--lh-base);
  --_ls: var(--ls-base);
  --_border-width: .0625rem;
  --_border-solid: solid;
  --_rounded: .5rem;
  --_padding: .25rem 1rem;
  --_color: inherit;
  --_color-hover: inherit;
  --_color-active: inherit;
  --_color-placeholder: var(--text-color-50);
  --_bg: var(--light);
  --_bg-hover: var(--light);
  --_bg-active: var(--light);
  --_border-color: var(--text-color-20);
  --_border-color-hover: currentColor;
  --_border-color-active: currentColor;
  width: var(--_width);
  min-height: var(--_height);
  display: block;
  font-size: var(--_fs);
  font-weight: var(--_fw);
  line-height: var(--_lh);
  letter-spacing: var(--_ls, normal);
  color: var(--_color);
  background-color: var(--_bg);
  border: var(--_border-width) var(--_border-solid) var(--_border-color);
  border-radius: var(--_rounded);
  padding: var(--_padding);
  outline: none;
  appearance: none;
  transition-property: color, background-color, border-color;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
}
.field::placeholder {
  font: inherit;
  color: var(--_color-placeholder);
}
.field.is-focus, .field:focus-visible {
  color: var(--_color-active);
  background-color: var(--_bg-active);
  border-color: var(--_border-color-active);
}

label.field {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.field--plaintext {
  max-width: 100%;
  width: auto;
  min-width: 0;
  flex: 1 1;
  display: block;
  appearance: none;
  margin: 0;
  border: none;
  padding: 0;
  outline: none;
}

.field--error {
  --_border-color: var(--error);
  --_border-color-hover: var(--error);
  --_border-color-active: var(--error);
}

textarea.field {
  --_padding: 1rem;
  resize: none;
}

.field-button {
  --_height: 100%;
  --_icon-size: .75rem;
  --_transform: translateY(-50%);
  --_transform-hover: translateY(-50%);
  --_transform-active: translateY(-50%);
