.icon {
	--_size: 1rem;
	--_color: currentColor;

	width: var(--_size);
	height: var(--_size);
	flex-shrink: 0;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	color: var(--_color);

	svg {
		width: 100%;
		height: 100%;
		display: block;
		object-fit: contain;
		object-position: center;
	}
}

.icon-container {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .icon {
    transition-property: opacity;
    transition-duration: var(--_transition-duration, var(--transition-duration));
    transition-timing-function: var(--_transition-timing-function, var(--transition-timing-function));

    &:last-child {
      opacity: 0;
      position: absolute;
      inset: 0;
    }
  }
}
